import { Component, Host, Prop, State, Element, h } from '@stencil/core';
import { TranslateService } from '../../../../services/translate.service';
import { UtilService } from '../../../../services/util.service';

@Component({
  tag: 'error-detail-modal',
  styleUrl: 'error-detail-modal.scss',
  shadow: true,
})
export class ErrorDetailModal {
  @Prop() job: any;
  @Prop() open: boolean;
  @State() maxFailedOIDLength: number;
  
  @Element() modal: any;
  public errorModalMaxHeight: number;

  private langTasks: any;
  public langCommon: any;
  private utilService: UtilService;

  componentWillLoad() {
    this.utilService = UtilService.getService();
    return Promise.resolve(true)
    .then(() => {
      return TranslateService.getService().getTranslate();
    })
    .then((res) => {
      const langTasks = res.customPrint.recentTasks;
      this.langTasks = langTasks;
      this.langCommon = res.common;
      // this.updateFailedOIDlength(this.job);
    })
  }
  
  componentDidRender() {
    this.updateFailedOIDlength(this.job);
  }
  private updateFailedOIDlength(job) {
    if (this.maxFailedOIDLength > 0) {
      return;
    }
    let len = 120;
    const helperDiv = this.modal.shadowRoot.querySelector('.helperSpan');
    if (job.failedInfo && job.failedInfo.length && helperDiv) {
      job.failedInfo.forEach((info) => {
        helperDiv.innerHTML = info.objectIds;
        if (helperDiv.offsetWidth > len) {
          len = helperDiv.offsetWidth;
        }
      });
    }
    this.maxFailedOIDLength = len;
    return len;
  }

  render() {
    return (
      <Host>
        {/* helper div the get the max width of the failed objectIds  */}
        <div class="helper">
          <div>
            <span class="helperSpan"></span>
          </div>
        </div>
        <calcite-modal open={this.open} aria-labelledby="modal-title" onCalciteModalClose={() => {this.open=false;}} id="example-modal">
          <div slot="header" id="modal-title">
            {this.langTasks.detailFailedOIDs}
            {this.job.resultInfo&&this.job.resultInfo.failedObjectIds&&this.job.resultInfo.failedObjectIds.length ? ' (' + this.job.resultInfo.failedObjectIds.length + ') ' : '' }
          </div>
          <div slot="content">
            <div class="error-table-outter" style={{maxHeight: `${this.errorModalMaxHeight}`, overflow:'auto'}}>
              { this.job&&this.job.failedInfo&&this.job.failedInfo.length ?
                <table class="table table-striped table-bordered error-table">
                <thead>
                  <tr>
                    <th class="objectId-td" style={{width: `${this.maxFailedOIDLength+16}px`}}>{this.langTasks.detailFailedOIDs}</th>
                    <th>{this.langTasks.detailStatusMsg}</th>
                  </tr>
                </thead>
                <tbody>
                  {this.job.failedInfo.map((detail) => {
                    return <tr>
                            <td>
                              <div class="objectId-td" style={{width: `${this.maxFailedOIDLength}px`}}>
                                {detail.objectIds}
                              </div>
                            </td>
                            <td>
                              <div class="message" innerHTML={this.utilService.parseMarkdown(detail.messages)}>
                              </div>
                            </td>
                          </tr>
                  })}
                </tbody>
              </table>
              : ''
            }
              
            </div>
          </div>
          {/* <calcite-button slot="primary" width="full">
            a{this.langCommon.ok}b
          </calcite-button> */}
        </calcite-modal>
        <slot></slot>
      </Host>
    );
  }

}
